    methods: {		
        OnpagemasterseqChange (data) {
        if (this.M_CO_Reason.pagemasterseq == data.id) return
        this.$nextTick(() => {
            this.M_CO_Reason.pagemasterseq = data.id
            this.M_CO_Reason.pagemasterseqLabel = data.label
            if (this.inputStatus != "VIEW") {
//{pagemasterseq}
            }
        })
        this.$forceUpdate()
        },
	
