        M_Insert() {
            var param = {			
                OptionSeq: this.getOptionSeq().OptionSeq,
                LineNo: this.$parent.data.page_order,
                reason_cd: this.M_CO_Reason.reasoncd,
                descs: this.M_CO_Reason.descs,
                page_master_seq: this.M_CO_Reason.pagemasterseq == '' || this.M_CO_Reason.pagemasterseq == null ? 'NULL' : this.M_CO_Reason.pagemasterseq ,
                user_input: this.getDataUser().user_id                 
            }

            this.postJSON(this.getUrlInsert(), param)
            .then((response) => {
                if(response == null) return

				//kalo ada hit api lagi,tolong script dibawah ini dipindahkan di dalam then'a ,setelah itu hapus comment ini
                this.$parent.resultInsert(response.Message)
					
				
            })
        },
        M_Update() {
            var param = {			
                OptionSeq: this.getOptionSeq().OptionSeq,
                LineNo: this.$parent.data.page_order,
                reason_cd: this.M_CO_Reason.reasoncd,
                descs: this.M_CO_Reason.descs,
                page_master_seq: this.M_CO_Reason.pagemasterseq == '' || this.M_CO_Reason.pagemasterseq == null ? 'NULL' : this.M_CO_Reason.pagemasterseq ,
                user_edit: this.getDataUser().user_id ,
                lastupdatestamp: this.M_CO_Reason.lastupdatestamp                
            }

            this.postJSON(this.getUrlUpdate(), param)
            .then((response) => {
                if(response == null) return

                //kalo ada hit api lagi,tolong script dibawah ini dipindahkan di dalam then'a ,setelah itu hapus comment ini
                this.$parent.resultUpdate(response.Message)
				
            })
        },
		M_ClearForm(){
            this.M_CO_Reason = {
                reasoncd: '',
                descs: '',
                pagemasterseq: 0,
                userinput: '',
                useredit: this.getDataUser().user_id,
                timeinput: '',
                timeedit: '',
                lastupdatestamp: 0,
                controlsequenceno: 0,
                menuname: '',
                rowid: 0
            }		
		},
        M_New(){
			this.$refs.ref_reasoncd.focus()
        },
        M_Edit(){
            this.$refs.ref_descs.focus()
        },
        M_Delete(dt){       

            var data = this.FormToABSList().getRowSelected()  
            var dataDelete = []

            
            data.forEach((value) => {
                dataDelete.push({
                    reason_cd: value.reason_cd,
                    lastupdatestamp: value.lastupdatestamp,
                    _Message_: ''
                })
            })
            
            var param = {
                OptionSeq: this.getOptionSeq().OptionSeq,
                LineNo: this.$parent.data.page_order,
                Data: dataDelete
            }
              
            this.postJSONMulti( this.getUrlDeleteMulti(), param )
            .then(response => {
                if (response == null) return

                this.$parent.resultDelete()
            })
        },        
        getDataBy (record) {
            var param = {
                OptionSeq: this.getOptionSeq().OptionSeq,
                LineNo: this.$parent.data.page_order,
                reason_cd: record.reason_cd
            }

            this.postJSON( this.getUrlById(), param )
            .then(response => {
                // response from API
                if(response == null) return
                
                var data = response.Data[0]
                this.M_CO_Reason = {
                    reasoncd: data.reason_cd,
                    descs: data.descs,
                    pagemasterseq: data.page_master_seq,
                    userinput: data.user_input,
                    useredit: data.user_edit,
                    timeinput: data.time_input,
                    timeedit: data.time_edit,
                    lastupdatestamp: record.lastupdatestamp,
                    controlsequenceno: data.control_sequence_no,
                    menuname: data.menu_name,
                    rowid: data.row_id
                }

                this.M_CO_Reason.pagemasterseqLabel = this.M_CO_Reason.pagemasterseq != null ? data.page_master_seq + this.separator + data.menu_name :'' 

                this.IEBy.Input = data.userinput
                this.IEBy.Edit = data.useredit
            })
        }

